
html,body{height:100%
}
.select-rating .rating-type{padding:18px 0;margin:0 18px;position:relative;font-size:0
}
.select-rating .rating-type:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.select-rating .rating-type .block{display:inline-block;padding:8px 12px;margin-right:8px;line-height:16px;border-radius:1px;font-size:12px;color:#4d555d
}
.select-rating .rating-type .block.active{color:#fff
}
.select-rating .rating-type .block .count{margin-left:2px;font-size:8px
}
.select-rating .rating-type .block.positive{background:rgba(0,160,220,0.2)
}
.select-rating .rating-type .block.positive.active{background:#00a0dc
}
.select-rating .rating-type .block.negative{background:rgba(77,85,93,0.2)
}
.select-rating .rating-type .block.negative.active{background:#4d555d
}
.select-rating .switch{padding:12px 18px;line-height:24px;border-bottom:1px solid rgba(7,17,27,0.1);color:#93999f;font-size:0
}
.select-rating .switch .on.icon-check_circle{color:#00c850
}
.select-rating .switch .icon-check_circle{display:inline-block;vertical-align:top;margin-right:4px;font-size:24px
}
.select-rating .switch .text{display:inline-block;vertical-align:top;font-size:12px
}



