
html,body{height:100%
}
.shop-cart{position:fixed;left:0;bottom:0;z-index:50;width:100%;height:48px
}
.shop-cart .content{display:-ms-flexbox;display:flex;color:rgba(255,255,255,0.4);background:#141d27
}
.shop-cart .content .content-left{font-size:0;-ms-flex:1;flex:1
}
.shop-cart .content .content-left .logo-wrapper{display:inline-block;position:relative;top:-10px;margin:0 12px;padding:6px;width:56px;height:56px;box-sizing:border-box;vertical-align:top;border-radius:50%;background:#141d27
}
.shop-cart .content .content-left .logo-wrapper .logo{width:100%;height:100%;font-size:24px;background:#2b343c;border-radius:50%;text-align:center;line-height:44px;color:rgba(255,255,255,0.4)
}
.shop-cart .content .content-left .logo-wrapper .logo.highlight{background:#00a0dc
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart{line-height:44px;font-size:24px;color:#80858a
}
.shop-cart .content .content-left .logo-wrapper .logo .icon-shopping_cart.highlight{color:#fff
}
.shop-cart .content .content-left .logo-wrapper .num{position:absolute;top:0;right:0;width:24px;height:16px;line-height:16px;text-align:center;border-radius:16px;font-size:9px;font-weight:700;color:#fff;background:#f01414;box-shadow:0 4px 8px 0 rgba(0,0,0,0.4)
}
.shop-cart .content .content-left .price{display:inline-block;vertical-align:top;margin-top:12px;line-height:24px;padding-right:12px;box-sizing:border-box;border-right:1px solid rgba(255,255,255,0.1);font-size:16px;font-weight:700
}
.shop-cart .content .content-left .price.highlight{color:#fff
}
.shop-cart .content .content-left .description{display:inline-block;vertical-align:top;margin:12px 0 0 12px;line-height:24px;font-size:10px
}
.shop-cart .content .content-right{-ms-flex:0 0 105px;flex:0 0 105px;width:105px
}
.shop-cart .content .content-right .pay{height:48px;line-height:48px;text-align:center;font-size:12px;font-weight:700
}
.shop-cart .content .content-right .pay.not-enough{background:#2b333b
}
.shop-cart .content .content-right .pay.enough{background:#00b43c;color:#fff
}
.shop-cart .ball-container .ball{position:fixed;left:32px;bottom:22px;z-index:200
}
.shop-cart .ball-container .ball.ball-enter-active{transition:all 0.5s cubic-bezier(0.49, -0.29, 0.75, 0.41)
}
.shop-cart .ball-container .ball.ball-enter-active .inner{width:16px;height:16px;border-radius:50%;background:#00a0dc;transition:all 0.5s linear
}
.shop-cart .shopcart-list{position:absolute;left:0;top:0;z-index:-1;width:100%;transform:translate3d(0, -100%, 0)
}
.shop-cart .shopcart-list.fold-enter-active,.shop-cart .shopcart-list.fold-leave-active{transition:all 0.5s
}
.shop-cart .shopcart-list.fold-enter,.shop-cart .shopcart-list.fold-leave-active{transform:translate3d(0, 0, 0)
}
.shop-cart .shopcart-list.fold-leave{transform:translate3d(0, -100%, 0)
}
.shop-cart .shopcart-list .list-header{height:40px;line-height:40px;padding:0 18px;background:#f3f5f7;border-bottom:1px solid rgba(7,17,27,0.1)
}
.shop-cart .shopcart-list .list-header .title{float:left;font-size:14px;color:#07111b
}
.shop-cart .shopcart-list .list-header .empty{float:right;font-size:12px;color:#00a0dc
}
.shop-cart .shopcart-list .list-content{padding:0 18px;max-height:217px;overflow:hidden;background:#fff
}
.shop-cart .shopcart-list .list-content .list-item{position:relative;padding:12px 0;box-sizing:border-box;position:relative
}
.shop-cart .shopcart-list .list-content .list-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.shop-cart .shopcart-list .list-content .list-item .name{line-height:24px;font-size:14px;color:#07111b
}
.shop-cart .shopcart-list .list-content .list-item .price{position:absolute;right:90px;bottom:12px;line-height:24px;font-size:14px;font-weight:700;color:#f01414
}
.shop-cart .shopcart-list .list-content .list-item .cartcontral-wrapper{position:absolute;right:0;bottom:6px
}
.list-mask{position:fixed;top:0;left:0;width:100%;height:100%;z-index:40;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background:rgba(7,17,27,0.6)
}
.list-mask.fade-enter-active,.list-mask.fade-leave-active{transition:all 0.5s;opacity:1
}
.list-mask.fade-enter,.list-mask.fade-leave-active{opacity:0;background:rgba(7,17,27,0)
}



