
html,body{height:100%
}
.rating-item{display:-ms-flexbox;display:flex;padding:18px 0;position:relative
}
.rating-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.rating-item .avatar{-ms-flex:0 0 28px;flex:0 0 28px;width:28px;margin-right:12px
}
.rating-item .avatar img{border-radius:50%
}
.rating-item .content{position:relative;-ms-flex:1;flex:1
}
.rating-item .content .name{margin-bottom:4px;line-height:12px;font-size:10px;color:#07111b
}
.rating-item .content .star-wrapper{margin-bottom:6px;font-size:0
}
.rating-item .content .star-wrapper .star{display:inline-block;margin-right:6px;vertical-align:top
}
.rating-item .content .star-wrapper .delivery{display:inline-block;vertical-align:top;line-height:12px;font-size:10px;color:#93999f
}
.rating-item .content .text{margin-bottom:8px;line-height:18px;color:#07111b;font-size:12px
}
.rating-item .content .recommend{line-height:16px;font-size:0
}
.rating-item .content .recommend .icon-thumb_up,.rating-item .content .recommend .item{display:inline-block;margin:0 8px 4px 0;font-size:9px
}
.rating-item .content .recommend .icon-thumb_up{color:#00a0dc
}
.rating-item .content .recommend .item{padding:0 6px;border:1px solid rgba(7,17,27,0.1);border-radius:1px;color:#93999f;background:#fff
}
.rating-item .content .time{position:absolute;top:0;right:0;line-height:12px;font-size:10px;color:#93999f
}



