
html,body{height:100%
}
.food{position:fixed;left:0;top:0;bottom:48px;z-index:30;width:100%;background-color:#fff
}
.food.leftin-enter-active,.food.leftin-leave-active{transition:all 0.4s ease;transform:translate3d(0, 0, 0)
}
.food.leftin-enter,.food.leftin-leave-active{transform:translate3d(100%, 0, 0)
}
.food .image-header{position:relative;width:100%;height:0;padding-top:100%
}
.food .image-header img{position:absolute;left:0;top:0;width:100%;height:100%;border:0
}
.food .image-header .back{position:absolute;top:10px;left:0
}
.food .image-header .back .icon-arrow_lift{display:block;padding:10px;font-size:20px;color:#fff
}
.food .content{position:relative;padding:18px
}
.food .content .title{font-size:14px;font-weight:700;color:#07111b;line-height:14px
}
.food .content .detail{line-height:10px;font-size:0;margin-top:8px;color:#93999f
}
.food .content .detail span{font-size:10px
}
.food .content .detail .count{margin-right:12px
}
.food .content .price{margin-top:8px;font-size:0;line-height:24px
}
.food .content .price .now{margin-right:12px;font-size:14px;color:#f01414;font-weight:700
}
.food .content .price .old{font-size:10px;color:#93999f;font-weight:700
}
.food .content .cartcontral-wrapper{position:absolute;right:12px;bottom:12px
}
.food .content .buy{position:absolute;right:18px;bottom:18px;z-index:10;height:24px;line-height:24px;padding:0 12px;box-sizing:border-box;border-radius:12px;font-size:10px;color:#fff;background:#00a0dc
}
.food .content .buy.fade-enter-active,.food .content .buy.fade-leave-active{transition:all 0.2s
}
.food .content .buy.fade-enter,.food .content .buy.fade-leave{opacity:0;z-index:-1
}
.food .info{padding:18px
}
.food .info .title{line-height:14px;margin-bottom:6px;font-size:14px;color:#07111b
}
.food .info .text{line-height:24px;padding:0 8px;font-size:12px;color:#4d555d
}
.food .rating{padding-top:18px
}
.food .rating .title{line-height:14px;margin-left:18px;font-size:14px;color:#07111b
}
.food .rating .rating-wrapper{padding:0 18px
}
.food .rating .rating-wrapper .rating-item{position:relative;padding:16px 0;position:relative
}
.food .rating .rating-wrapper .rating-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.food .rating .rating-wrapper .rating-item .user{position:absolute;right:0;top:16px;line-height:12px;font-size:0
}
.food .rating .rating-wrapper .rating-item .user .name{display:inline-block;margin-right:6px;vertical-align:top;font-size:10px;color:#93999f
}
.food .rating .rating-wrapper .rating-item .user .avatar{border-radius:50%
}
.food .rating .rating-wrapper .rating-item .time{margin-bottom:6px;line-height:12px;font-size:10px;color:#93999f
}
.food .rating .rating-wrapper .rating-item .text{line-height:16px;font-size:12px;color:#07111b
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up,.food .rating .rating-wrapper .rating-item .text .icon-thumb_down{margin-right:4px;line-height:16px;font-size:12px
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_up{color:#00a0dc
}
.food .rating .rating-wrapper .rating-item .text .icon-thumb_down{color:#93999f
}
.food .rating .rating-wrapper .no-rating{padding:16px 0;font-size:12px;color:#93999f
}



