
html,body{height:100%
}
.goods{display:-ms-flexbox;display:flex;position:absolute;top:174px;bottom:46px;width:100%;overflow:hidden
}
.goods .menu-wrapper{-ms-flex:0 0 80px;flex:0 0 80px;width:80px;background:#f3f5f7
}
.goods .menu-wrapper .menu-item{display:table;width:56px;height:54px;line-height:14px;padding:0 12px
}
.goods .menu-wrapper .menu-item.current{position:relative;margin-top:-1px;background:#fff
}
.goods .menu-wrapper .menu-item.current .text{font-weight:700
}
.goods .menu-wrapper .menu-item.current .text:after{display:none
}
.goods .menu-wrapper .menu-item .text{display:table-cell;width:56px;vertical-align:middle;position:relative;font-size:12px
}
.goods .menu-wrapper .menu-item .text:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.goods .menu-wrapper .menu-item .icon{display:inline-block;vertical-align:top;width:12px;height:12px;margin-right:2px;background-size:12px 12px;background-repeat:no-repeat
}
.goods .menu-wrapper .menu-item .icon.decrease{background-image:url("decrease_3@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.goods .menu-wrapper .menu-item .icon.decrease{background-image:url("decrease_3@3x.png")
}
}
.goods .menu-wrapper .menu-item .icon.discount{background-image:url("discount_3@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.goods .menu-wrapper .menu-item .icon.discount{background-image:url("discount_3@3x.png")
}
}
.goods .menu-wrapper .menu-item .icon.guarantee{background-image:url("guarantee_3@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.goods .menu-wrapper .menu-item .icon.guarantee{background-image:url("guarantee_3@3x.png")
}
}
.goods .menu-wrapper .menu-item .icon.invoice{background-image:url("invoice_3@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.goods .menu-wrapper .menu-item .icon.invoice{background-image:url("invoice_3@3x.png")
}
}
.goods .menu-wrapper .menu-item .icon.special{background-image:url("special_3@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.goods .menu-wrapper .menu-item .icon.special{background-image:url("special_3@3x.png")
}
}
.goods .foods-wrapper{-ms-flex:1;flex:1
}
.goods .foods-wrapper .food-list .title{padding-left:14px;height:26px;line-height:26px;border-left:2px solid #d9dde1;font-size:12px;color:#93999f;background-color:#f3f5f7
}
.goods .foods-wrapper .food-list .food-item{display:-ms-flexbox;display:flex;margin:18px;padding-bottom:18px;position:relative
}
.goods .foods-wrapper .food-list .food-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.goods .foods-wrapper .food-list .food-item:last-child{margin-bottom:0
}
.goods .foods-wrapper .food-list .food-item:last-child:after{display:none
}
.goods .foods-wrapper .food-list .food-item .icon{-ms-flex:0 0 57px;flex:0 0 57px;margin-right:10px
}
.goods .foods-wrapper .food-list .food-item .icon img{width:57px;height:57px
}
.goods .foods-wrapper .food-list .food-item .content{margin-top:2px
}
.goods .foods-wrapper .food-list .food-item .content .name{line-height:14px;font-size:14px;color:#07111b
}
.goods .foods-wrapper .food-list .food-item .content .description{margin-top:8px;font-size:10px;line-height:10px;color:#93999f
}
.goods .foods-wrapper .food-list .food-item .content .extra{margin-top:8px;font-size:0;color:#93999f;line-height:10px
}
.goods .foods-wrapper .food-list .food-item .content .extra span{display:inline-block;font-size:10px
}
.goods .foods-wrapper .food-list .food-item .content .extra span:first-child{margin-right:12px
}
.goods .foods-wrapper .food-list .food-item .content .price{font-size:0;color:#93999f;margin-top:8px
}
.goods .foods-wrapper .food-list .food-item .content .price .count{margin-right:8px;color:red
}
.goods .foods-wrapper .food-list .food-item .content .price span{display:inline-block;font-size:14px;font-weight:700
}
.goods .foods-wrapper .food-list .food-item .content .price span:last-child{font-size:10px
}
.goods .foods-wrapper .food-list .food-item .content .price span span{font-size:10px
}
.goods .foods-wrapper .food-list .food-item .content .cartcontral-wrapper{position:absolute;right:0;bottom:8px
}



