
.cartcontral{font-size:0
}
.cartcontral .minus{display:inline-block;padding:6px
}
.cartcontral .minus .inner{display:inline-block;line-height:24px;font-size:24px;color:#00a0dc;transform:rotate(0deg)
}
.cartcontral .minus.move-enter-active,.cartcontral .minus.move-leave-active{transform:translate3d(0, 0, 0);transition:all .4s linear
}
.cartcontral .minus.move-enter-active .inner,.cartcontral .minus.move-leave-active .inner{transition:all .4s linear
}
.cartcontral .minus.move-enter,.cartcontral .minus.move-leave-active{opacity:0;transform:translate3d(24px, 0, 0)
}
.cartcontral .minus.move-enter .inner,.cartcontral .minus.move-leave-active .inner{transform:rotate(180deg)
}
.cartcontral .addition{display:inline-block;padding:6px;line-height:24px;font-size:24px;color:#00a0dc
}
.cartcontral .count{display:inline-block;vertical-align:top;width:12px;padding-top:6px;line-height:24px;text-align:center;font-size:10px;color:#93999f
}



