
html,body{height:100%
}
.seller{position:absolute;top:174px;bottom:0;left:0;width:100%;overflow:hidden
}
.seller .overview{position:relative;padding:18px
}
.seller .overview .title{margin-bottom:8px;line-height:14px;color:#07111b;font-size:14px
}
.seller .overview .desc{padding-bottom:18px;position:relative;font-size:0
}
.seller .overview .desc:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.seller .overview .desc .star{display:inline-block;margin-right:8px;vertical-align:top
}
.seller .overview .desc .text{display:inline-block;margin-right:12px;line-height:18px;vertical-align:top;font-size:10px;color:#4d555d
}
.seller .overview .remark{display:-ms-flexbox;display:flex;padding-top:18px
}
.seller .overview .remark .block{-ms-flex:1;flex:1;text-align:center;border-right:1px solid rgba(7,17,27,0.1)
}
.seller .overview .remark .block:last-child{border:none
}
.seller .overview .remark .block h2{margin-bottom:4px;line-height:10px;font-size:10px;color:#93999f
}
.seller .overview .remark .block .content{line-height:24px;font-size:10px;color:#07111b
}
.seller .overview .remark .block .content .stress{font-size:24px
}
.seller .overview .favorite{position:absolute;width:50px;right:11px;top:18px;text-align:center
}
.seller .overview .favorite .icon-favorite{display:block;margin-bottom:4px;line-height:24px;font-size:24px;color:#d4d6d9
}
.seller .overview .favorite .icon-favorite.active{color:#f01414
}
.seller .overview .favorite .text{line-height:10px;font-size:10px;color:#4d555d
}
.seller .bulletin{padding:18px 18px 0 18px
}
.seller .bulletin .title{margin-bottom:8px;line-height:14px;color:#07111b;font-size:14px
}
.seller .bulletin .content-wrapper{padding:0 12px 16px 12px;position:relative
}
.seller .bulletin .content-wrapper:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.seller .bulletin .content-wrapper .content{line-height:24px;font-size:12px;color:#f01414
}
.seller .bulletin .supports .support-item{padding:16px 12px;position:relative;font-size:0
}
.seller .bulletin .supports .support-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.seller .bulletin .supports .support-item:after{display:none
}
.seller .bulletin .supports .icon{display:inline-block;width:16px;height:16px;vertical-align:top;margin-right:6px;background-size:16px 16px;background-repeat:no-repeat
}
.seller .bulletin .supports .icon.decrease{background-image:url("decrease_4@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
