}
}
.seller .bulletin .supports .icon.discount{background-image:url("discount_4@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.seller .bulletin .supports .icon.discount{background-image:url("discount_4@3x.png")
}
}
.seller .bulletin .supports .icon.guarantee{background-image:url("guarantee_4@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.seller .bulletin .supports .icon.guarantee{background-image:url("guarantee_4@3x.png")
}
}
.seller .bulletin .supports .icon.invoice{background-image:url("invoice_4@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.seller .bulletin .supports .icon.invoice{background-image:url("invoice_4@3x.png")
}
}
.seller .bulletin .supports .icon.special{background-image:url("special_4@2x.png")
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3){
.seller .bulletin .supports .icon.special{background-image:url("special_4@3x.png")
}
}
.seller .bulletin .supports .text{line-height:16px;font-size:12px;color:#07111b
}
.seller .pics{padding:18px
}
.seller .pics .title{margin-bottom:12px;line-height:14px;color:#07111b;font-size:14px
}
.seller .pics .pic-wrapper{width:100%;overflow:hidden;white-space:nowrap
}
.seller .pics .pic-wrapper .pic-list{font-size:0
}
.seller .pics .pic-wrapper .pic-list .pic-item{display:inline-block;margin-right:6px;width:120px;height:90px
}
.seller .pics .pic-wrapper .pic-list .pic-item:last-child{margin:0
}
.seller .info{padding:18px 18px 0 18px;color:#07111b
}
.seller .info .title{padding-bottom:12px;line-height:14px;position:relative;font-size:14px
}
.seller .info .title:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.seller .info .info-item{padding:16px 12px;line-height:16px;position:relative;font-size:12px
}
.seller .info .info-item:after{display:block;position:absolute;left:0;bottom:0;width:100%;content:' ';border-top:1px solid rgba(7,17,27,0.1)
}
.seller .info .info-item:last-child:after{display:none
}



